                          showDialCodeInSelection: true,
                        }"
                        :inputOptions="{
                          placeholder: this.placeHolderText(),
                          type: 'number',
                        }"
                        @validate="isNumber_mobile"
                        @country-changed="countryChanged"
                        :validCharactersOnly="true"
                        inputOptions.placeholder=""
                        mode="international"
                        id="create_for_mobile"
                        :enabledCountryCode="true"
                        inputId="create_for_mobile"
                      ></vue-tel-input>
                      <div class="tel-error" v-show="!this.isValid">
