<script>
import Vue from "vue";
import { analytics, setLocale } from "../services/firebase";
import i18n from "../i18n";
import userOrderList from "./userOrderList";
import userCatelogueList from "./userCatelogueList";
import { save, sendOtpMessage, downloadUrl } from "../services/firebase";
import store from "../store/store";
export default {
  name: "GsCatelogueForm",
  props: [""],

  data() {
    // var category_list = [];
    return {
      // category_list,
      Initial_OTP: false,
      OTP_text: "",
      form_otp: false,
      // mobile: "",
      OTP_mobile: "",
      mobileno: "",
      OTP: "",
      countDown: 60,
      toggleOTP: false,
      form_otp_desktop: false,
      form_place_order_desktop: false,
      form_place_order: false,
      form_submit: false,
      submit_catelogue_form: false,
      form_submit_desktop: false,
      submit_catelogue_form_desktop: false,
      set_overlay: false,
      counter_flag: true,
      previous_number: "",
      country: {},
      valid: true,
      payload: {},
      list: [],
    };
  },

  computed: {
    isEditable: function() {
      if (this.$route.name === "viewform") return false;
      return true;
    },
    type: function() {
      if (this.payload.type == "Ordering form") {
        return i18n.t("common.typeOrderForm");
      } else {
        return i18n.t("common.typeCatelogue");
      }
    },
    category_list: function() {
      var catName = "";
      let data = "";
      if (this.payload.catalogue) {
        for (var i = 0; i < this.payload.catalogue.length; i++) {
          let list = this.payload.catalogue[i];
          if (list.type == "category") {
            catName = list.name;
          }
          if (list.type == "item" && list.category == undefined) {
            list.category = catName;
          }
        }
        data = this.payload.catalogue.filter(
          (o) => o.type == "category" && o.name != ""
        );
        data.forEach((list) => {
          list.items = [];
          if (this.payload.catalogue) {
            list.items = this.payload.catalogue.filter(
              (o) => o.category == list.name
            );
          }
        });
      }
      return data;
    },
    no_order_selected: function() {
      let qtt = 0;
      if (this.payload.type == "Ordering form") {
        let items = this.payload.catalogue.filter((o) => o.type == "item");
        for (var item of items) {
          qtt += item.qtty;
        }
      } else {
        if (this.payload.catalogue) {
          let items = this.payload.catalogue.filter((o) => o.type == "item");
          for (var p_item of items) {
            qtt += p_item.qtty;
          }
        }
      }
      return qtt;
    },
    isValid: function() {
      return this.mobileno === "" ? true : this.valid;
    },
  },
  mounted() {
    setLocale();
    this.fetchData_();
    this.payload.amount = 0;
  },
  components: {
    userOrderList,
    userCatelogueList,
  },
  methods: {
    countryChanged(country) {
      this.country = country;
    },

    placeHolderText() {
      var msgPlaceHolder = i18n.t("newUi.OTPFlow.MobileNumber");
      return msgPlaceHolder;
    },
    submit_order_catalogue: async function() {
      if (this.payload.amount > 0) {
        this.submit_catelogue_form = true;
      } else {
        alert(i18n.t("alertText.Placecatalogue"));
