      }
    },
    submit_order_desktop_catalogue: async function() {
      if (this.payload.amount > 0) {
        this.submit_catelogue_form_desktop = true;
      } else {
        alert(i18n.t("alertText.Placecatalogue"));
      }
    },
    isNumber(phoneObject) {
      this.OTP = "";
      if (phoneObject.nationalNumber) {
        this.valid = phoneObject.valid;
      }
      let Phone =
        phoneObject.countryCallingCode + "" + phoneObject.nationalNumber;
      if (this.previous_number != "" && this.previous_number != Phone) {
        this.OTP = "";
        if (this.countDown != 0 && this.countDown != 60) {
          this.counter_flag = false;
          setTimeout(() => {
            this.countDown = 0;
            this.OTP_text = i18n.t("newUi.OTPFlow.GetOTP");
          }, 1000);
        } else {
          this.OTP_text = i18n.t("newUi.OTPFlow.GetOTP");
        }
      } else if (this.previous_number != "" && this.previous_number == Phone) {
        this.OTP_text = i18n.t("newUi.OTPFlow.resendOTP");
      }
    },
    isNumber_mobile(phoneObject) {
      this.OTP_mobile = "";
      if (phoneObject.nationalNumber) {
        this.valid = phoneObject.valid;
      }
      let Phone =
        phoneObject.countryCallingCode + "" + phoneObject.nationalNumber;
      if (this.previous_number != "" && this.previous_number != Phone) {
        if (this.countDown != 0 && this.countDown != 60) {
          this.counter_flag = false;
          setTimeout(() => {
            this.countDown = 0;
            this.OTP_text = i18n.t("newUi.OTPFlow.GetOTP");
          }, 1000);
        } else {
          this.OTP_text = i18n.t("newUi.OTPFlow.GetOTP");
        }
      } else if (this.previous_number != "" && this.previous_number == Phone) {
        this.OTP_text = i18n.t("newUi.OTPFlow.resendOTP");
      }
    },
    IsOTP(type) {
      if (type == "Desktop") {
        this.OTP = this.OTP.replace(/\D/g, "");
      } else if (type === "Mobile") {
        this.OTP_mobile = this.OTP_mobile.replace(/\D/g, "");
      }
    },
    closing_OTP_desktop() {
      this.form_otp_desktop = false;
      this.mobileno = "";
      this.Initial_OTP = false;
      this.previous_number = "";
      this.OTP = "";
      this.counter_flag = false;
      this.valid = true;
      setTimeout(() => {
        this.countDown = 0;
        this.otpText();
      }, 1000);
    },
    closing_OTP() {
      this.form_otp = false;
      this.mobileno = "";
      this.Initial_OTP = false;
      this.previous_number = "";
      this.OTP_mobile = "";
      this.valid = true;
      this.counter_flag = false;
      setTimeout(() => {
        this.countDown = 0;
        this.otpText();
      }, 1000);
    },
    closing_place_order_desktop() {
      this.form_place_order_desktop = false;
      this.$router.go();
    },
    closing_place_order() {
      this.form_place_order = false;
      this.$router.go();
    },
    submitOTP() {
      var OTP = document.getElementById("inputOTP").value;
      console.log(OTP);
      var regx = /^[0-9]\d{3}$/;
      if (regx.test(OTP)) {
        alert(OTP);
        //this.countDownTimer();
        console.log("valid OTP");
        // api call for validating OTP
      } else alert(OTP);
    },
    fetchData_() {
      this.set_overlay = true;

      if (this.$route.params.formid) {
        // this.loadLocale_(this.$route.params.formid, function() {});
      }
      // var state = this.$store.state;
      // console.log('state',state);
      // state.currentFormPos = this.$route.params.formpos;
      // var payload=this.payload;
      this.payload.FormPos = this.$route.params.formpos;
      // var store = this.$store;
      var elethis = this;
      this.loadFile_(
        this.$route.params.formid,
        this.$route.params.formpos,
        function(error) {
          if (error) {
            //TODO skeleton data
            Vue.nextTick(() => {
              elethis.set_overlay = false;
              // store.dispatch("setOverlay", false);
            });
          } else {
            if (elethis.payload.catalogue) {
              for (var i = 0; i < elethis.payload.catalogue.length; i++) {
                elethis.payload.catalogue[i].qtty = 0;
                elethis.payload.catalogue[i].isOrdered = false;
              }
            }
            Vue.nextTick(() => {
              //controller.resume();
              elethis.set_overlay = false;
              // store.dispatch("setOverlay", false);
            });
          }
        }
      );
    },
    loadLocale_(id, callback) {
      downloadUrl("locale.json")
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          fetch(data.message, {
            method: "GET",
            redirect: "follow",
          })
            .then((response) => response.text())
            .then((result) => {
              result = JSON.parse(result);
              i18n.locale = result.locale;
              callback(null);
            })
            .catch((error) => {
              i18n.locale = "en";
              console.log("error", error);
            });
        })
        .catch((error) => {
          i18n.locale = "en";
          console.log("Error", error);
        });
    },
    loadFile_(id, pos, callback) {
      var elethis = this;
      downloadUrl("catalogue" + pos + ".json")
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          fetch(data.message, {
            method: "GET",
            redirect: "follow",
          })
            .then((response) => response.text())
            .then((result) => {
              //console.log(result);
              var strval = result;
              if (!strval) strval = "[]";
              var newfile = JSON.parse(strval);
              // debugger;
              // state.currentForm.id = appid;
              // setCurrentFormData(newfile, state);
              // store.dispatch("setCurrentFormData", newfile);
              elethis.payload.id = id;
              elethis.payload.pos = pos;
              elethis.payload.createdOn = newfile.createdOn;
              elethis.payload.currency = newfile.currency;
              elethis.payload.symbol = newfile.symbol;
              elethis.payload.shopname = newfile.shopname;
              elethis.payload.shopdesc = newfile.shopdesc;
              elethis.payload.type = newfile.type;
              elethis.payload.nationalNumber = newfile.nationalNumber;
              elethis.payload.phone = newfile.phone;
              if (elethis.payload.catalogue) {
                while (elethis.payload.catalogue.length > 0)
                  elethis.payload.catalogue.pop();
              } else {
                elethis.payload.catalogue = [];
              }

              if (!newfile.catalogue) newfile.catalogue = [];
              for (var i = 0; i < newfile.catalogue.length; i++)
                elethis.payload.catalogue.push(newfile.catalogue[i]);

              var maxpos = 0;

              for (var j = 0; j < newfile.catalogue.length; j++)
                if (newfile.catalogue[j].pos >= maxpos)
                  maxpos = newfile.catalogue[j].pos + 1;

              elethis.payload.maxlength = maxpos;
              // this.payload.catalogue.maxlength = maxpos;
              callback(null);
            })
            .catch((error) => {
              console.log("error", error);
              callback(error);
            });
        })
        .catch((error) => {
          console.log("Error", error);
          callback(error);
        });
    },
    async sendOTP() {
      if (this.country.dialCode && this.mobileno && this.mobileno.length > 1) {
        var phone = this.country.dialCode + "" + this.mobileno;
        if (this.isValid) {
          this.previous_number = phone;
          this.countDown = 60;
          this.counter_flag = true;
          this.countDownTimer();
          phone = parseInt(phone);
          if (this.country.dialCode == 91) {
            const msg = await sendOtpMessage(phone, false);
            if (msg != "") {
              alert("You are re-trying too early. Please wait for some time");
            }
            this.Initial_OTP = true;
          } else {
            const msg = await sendOtpMessage(phone, true);
            if (msg != "") {
              alert("You are re-trying too early. Please wait for some time");
            }
            this.Initial_OTP = true;
          }
        } else alert(i18n.t("newUi.OTPFlow.invalidPhone"));
      } else if (!this.country.dialCode) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterCountryCode"));
      } else if (!this.mobileno || this.mobileno.length == 1) {
        alert(i18n.t("newUi.OTPFlow.kindlyEnterPhoneNumber"));
      }
      // this.countDownTimer()
    },
    async sendOTP_Mobile() {
      if (this.country.dialCode && this.mobileno && this.mobileno.length > 1) {
        var phone = this.country.dialCode + "" + this.mobileno;
        // var regx = /^[6-9]\d{9}$/;
        if (this.isValid) {
          this.previous_number = phone;
          this.countDown = 60;
          this.counter_flag = true;
          this.countDownTimer();
          phone = parseInt(phone);
          if (this.country.dialCode == 91) {
            const msg = await sendOtpMessage(phone, false);
            if (msg != "") {
              alert("You are re-trying too early. Please wait for some time");
            }
            this.Initial_OTP = true;
          } else {
            const msg = await sendOtpMessage(phone, true);
            if (msg != "") {
              alert("You are re-trying too early. Please wait for some time");
            }
            this.Initial_OTP = true;
          }
        } else alert(i18n.t("newUi.OTPFlow.invalidPhone"));
      } else if (!this.country.dialCode) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterCountryCode"));
      } else if (!this.mobileno || this.mobileno.length == 1) {
        alert(i18n.t("newUi.OTPFlow.kindlyEnterPhoneNumber"));
      }
      // this.countDownTimer()
    },
    countDownTimer() {
      if (this.countDown > 0 && this.counter_flag) {
        this.toggleOTP = true;
        this.countDown -= 1;
        setTimeout(() => {
          this.otpText();
          this.countDownTimer();
        }, 1000);
      } else {
        this.countDown = 60;
        this.toggleOTP = false;
      }
    },
    otpText() {
      if (this.countDown === 0 || this.countDown === 60) {
        if (this.Initial_OTP) this.OTP_text = i18n.t("newUi.OTPFlow.resendOTP");
        else this.OTP_text = i18n.t("newUi.OTPFlow.GetOTP");
      } else {
        this.OTP_text =
          i18n.t("newUi.OTPFlow.resendOTP") +
          " (" +
          this.countDown +
          " " +
          i18n.t("newUi.OTPFlow.seconds") +
          ")";
        // this.toggleOTP=false;
      }
    },
    OTP_Verification: async function() {
      if (!this.mobileno || this.mobileno.length == 1) {
        alert(i18n.t("newUi.OTPFlow.kindlyEnterPhoneNumber"));
      } else if (!this.country.dialCode) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterCountryCode"));
      } else if (!this.OTP) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterOTP"));
      } else if (this.OTP) {
        var regx = /^[0-9]\d{3}$/;
        // var regx2 = /^[6-9]\d{9}$/;
        // var regx3=/^(\+\d{1,2}\s?)?1?\-?\.?\s?\(?\d{3}\)?[\s.-]?\d{3}[\s.-]?\d{4}$/gm;
        // const phone=this.country.dialCode+""+this.mobileno;
        if (!this.isValid) {
          alert(i18n.t("newUi.OTPFlow.invalidPhone"));
        } else if (!regx.test(this.OTP)) {
          this.OTP = "";
          alert(i18n.t("newUi.OTPFlow.invalidOTP"));
          console.log("valid OTP");
          // api call for validating OTP
        } else {
          this.form_otp_desktop = false;
          this.set_overlay = true;
          let response = await save(
            this.payload,
            this.country.dialCode,
            this.mobileno,
            this.OTP
          );
          // console.log('response',response);
          if (response.Order_id) {
            let Order_id = response.Order_id;
            this.payload.OrderId = Order_id;
            this.set_overlay = false;
            store.dispatch("setOrderId", Order_id);
            this.form_place_order_desktop = true;
          } else {
            this.set_overlay = false;
            this.OTP = "";
            this.form_otp_desktop = true;
            let errorMessage = response.message;
            if (errorMessage == "Kindly Generate New OTP") {
              errorMessage = i18n.t("newUi.OTPFlow.newOTP");
            } else if (errorMessage == "Please enter a valid OTP") {
              errorMessage = i18n.t("newUi.OTPFlow.invalidOTPServer");
            } else {
              errorMessage = i18n.t("newUi.OTPFlow.somethingWentWrong");
            }

            setTimeout(() => {
              alert(errorMessage);
            }, 500);
          }
        }
      }
    },
    OTP_Verification_mobile: async function() {
      if (!this.mobileno || this.mobileno.length == 1) {
        alert(i18n.t("newUi.OTPFlow.kindlyEnterPhoneNumber"));
      } else if (!this.country.dialCode) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterCountryCode"));
      } else if (!this.OTP_mobile) {
        alert(i18n.t("newUi.OTPFlow.KindlyEnterOTP"));
      } else if (this.OTP_mobile) {
        // const phone=this.country.dialCode+""+this.mobile;
        // const value= isValidPhoneNumber(phone,this.country.iso2);
        var regx = /^[0-9]\d{3}$/;
        // var regx2 = /^[6-9]\d{9}$/;
        // var regx3=/^(\+\d{1,2}\s?)?1?\-?\.?\s?\(?\d{3}\)?[\s.-]?\d{3}[\s.-]?\d{4}$/gm;

        if (!this.isValid) {
          alert(i18n.t("newUi.OTPFlow.invalidPhone"));
        } else if (!regx.test(this.OTP_mobile)) {
          this.OTP_mobile = "";
          alert(i18n.t("newUi.OTPFlow.invalidOTP"));
          // api call for validating OTP
        } else {
          this.form_otp = false;
          this.set_overlay = true;
          let response = await save(
            this.payload,
            this.country.dialCode,
            this.mobileno,
            this.OTP_mobile
          );
          if (response.Order_id) {
            let Order_id = response.Order_id;
            this.payload.OrderId = Order_id;
            this.set_overlay = false;
            store.dispatch("setOrderId", Order_id);
            this.form_place_order = true;
          } else {
            this.set_overlay = false;
            this.OTP_mobile = "";
            this.form_otp = true;
            let errorMessage = response.message;

            if (errorMessage == "Kindly Generate New OTP") {
              errorMessage = i18n.t("newUi.OTPFlow.newOTP");
            } else if (errorMessage == "Please enter a valid OTP") {
              errorMessage = i18n.t("newUi.OTPFlow.invalidOTPServer");
            } else {
              errorMessage = i18n.t("newUi.OTPFlow.somethingWentWrong");
            }

            setTimeout(() => {
              alert(errorMessage);
            }, 500);
          }
        }
      }
    },
    navws: async function() {
      var phoneno = this.payload.phone;
      var msg = this.composeMessage(this.payload);
      var url = this.createWhatsappUrl(phoneno, msg);

      let eventName = this.checkEventName();
      this.generateAnalytics(eventName, phoneno, msg);
      window.location.href = url;
    },
    navwsCatalog: async function() {
      var phoneno = this.payload.phone;
      var msg = this.composeMessageCatalog(this.payload);
      var url = this.createWhatsappUrl(phoneno, msg);

      let eventName = this.checkEventName();
      this.generateAnalytics(eventName, phoneno, msg);
      window.location.href = url;
    },
    createWhatsappUrl: function(phoneno, msg) {
      var url =
        "https://wa.me/" +
        encodeURI(phoneno) +
        "/?text=" +
        encodeURIComponent(msg);
      return url;
    },
    checkEventName: function() {
      let eventName = "wa_order_form";
      if (this.payload.type !== "Ordering form") {
        eventName = "wa_catalogue";
      }
      return eventName;
    },
    generateAnalytics: function(eventName, phoneno, msg) {
      analytics.logEvent(eventName, {
        phone: phoneno,
        msg: msg,
      });
    },
    composeMessageCatalog: function(form) {
      var msg =
        i18n.t("WhatsAppText.fillForm.catelogueText") +
        " \n\n*" +
        i18n.t("WhatsAppText.fillForm.header1") +
        "* x *" +
        i18n.t("WhatsAppText.fillForm.header3") +
        "*\n" +
        "";

      for (var i = 0; i < form.catalogue.length; i++) {
        var curitem = form.catalogue[i];
        if (curitem.type !== "item") continue;
        if (form.type === "Catalogue" && !curitem.isOrdered) continue;

        msg +=
          "" +
          (curitem.name +
            " " +
            (form.type === "Ordering form" ? " x " + curitem.qtty : "") +
            (form.type === "Catalogue" ? " : " + curitem.price : "")) +
          " \n";
      }
      msg +=
        "*" +
        i18n.t("WhatsAppText.fillForm.total") +
        "*\t" +
        form.symbol +
        form.amount +
        "\n";

      msg += "\n" + i18n.t("WhatsAppText.fillForm.confirm") + "\n";

      return msg;
    },
    composeMessage: function() {
      var msg =
        i18n.t("WhatsAppText.Order_confirmation") + " " + this.payload.OrderId;
      /* (form.type === "Ordering form"
            ? i18n.t("WhatsAppText.fillForm.orderFormText") +
              " \n\n*" +
              i18n.t("WhatsAppText.fillForm.header1") +
              "* x *" +
              i18n.t("WhatsAppText.fillForm.header2") +
              "*\n"
            : i18n.t("WhatsAppText.fillForm.catelogueText") +
              " \n\n*" +
              i18n.t("WhatsAppText.fillForm.header1") +
              "* x *" +
              i18n.t("WhatsAppText.fillForm.header3") +
              "*\n") + "";
        console.log('initial',msg);
        for (var i = 0; i < form.catalogue.length; i++) {
          var curitem = form.catalogue[i];
          if (curitem.type !== "item") continue;
          if (form.type === "Ordering form" && curitem.qtty < 1) continue;
          if (form.type === "Catalogue" && !curitem.isOrdered) continue;

          msg +=
            "" +
            (curitem.name +
              " " +
              (form.type === "Ordering form" ? " x " + curitem.qtty : "") +
              (form.type === "Catalogue" ? " x " + curitem.price : "")) +
            " \n";
        }
        console.log('Middle',msg);
        if (form.type === "Ordering form") {
          msg +=
            "*" +
            i18n.t("WhatsAppText.fillForm.total") +
            "*\tRs. " +
            form.amount +
            "\n";
        } else {
          msg +=
            "*" +
            i18n.t("WhatsAppText.fillForm.total") +
            "*\tRs. " +
            form.amount +
            "\n";
        }
        msg += "\n" + i18n.t("WhatsAppText.fillForm.confirm") + "\n";

        //alert("msg: " + msg);
        console.log('End',msg);*/
      return msg;
    },
  },
};
</script>
