<template>
  <div class="row item_box product_card">
    <!-- {{list.pos}} -->
    <div class="imageview">
      <div class="viewform_images_list">
        <img
          class="justify-start item-box"
          style="width: 100%; object-fit: cover;"
          v-if="list.imageurl"
          alt="image"
          :src="list.imageurl"
        />
        <div v-else>
          <img
            class="noPreview"
            src="../assets/img/no_preview.png"
            alt="no_preview"
          />
          <p>{{ $t("newUi.adminViewform.noPreview") }}</p>
        </div>
      </div>
    </div>

    <!-- <div class="image_box justify-start" v-if="!list.imageurl"></div> -->
    <div class="col column-padding">
      <div class="item_text">
        <p>{{ list.name }}</p>
      </div>
      <read-more
        class="text_body"
        :more-str="$t('newUi.adminViewform.readMore')"
        :text="list.desc"
        link="#"
        :less-str="$t('newUi.adminViewform.readLess')"
        :max-chars="50"
      ></read-more>
      <div class="d-flex justify-space-between">
        <div class="item_text justify-start item_price">
          <span style="margin-right: 3px">{{ payload.symbol }}</span
          >{{ parseFloat(list.price).toFixed(2) }}
        </div>
        <div class="justify-end">
          <div
            class="text-end user_order_add_button1 cursor-pointer"
            v-if="list.qtty == 0"
            @click="incr_one(list.pos)"
          >
            <span>+ </span>
            <span>{{ $t("newUi.userFlow.add") }}</span>
          </div>
          <div class="text-end user_order_add_button1" v-if="list.qtty != 0">
            <span
              style="font-size: 15px; padding: 5px;  padding-left:10px; padding-right:10px"
              class="cursor-pointer"
              @click="dcr_one(list.pos)"
            >
              -
            </span>
            <span class="icon-space">{{ list.qtty }}</span>
            <span
              style="font-size: 15px; padding: 5px; padding-left:10px; padding-right:10px"
              class="cursor-pointer"
              @click="incr_one(list.pos)"
            >
              +
            </span>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="button-position">
            
            </div> -->
  </div>
</template>
<script>
import { setLocale } from "../services/firebase";
import i18n from "../i18n";
// import NumInp from "./NumInp"

export default {
  props: ["payload", "list"],
  // components: {
  //         NumInp
  //     },
  data() {
    return {
      dialog: false,
      edit_product: false,
      edit_category: false,
      delete_product: false,
      npayload: this.payload,
      nlist: this.list,
    };
  },
  mounted() {
    if (window.location.href.split("?")[1]) {
      if (window.location.href.split("?")[1].split("=")[1])
        i18n.locale = window.location.href.split("?")[1].split("=")[1];
      else setLocale();
    } else setLocale();
  },

  // computed: {
  //     typecheck: function() {
  //        return console.log("edit value" + this.edit_product)
  //     },
  // },
  methods: {
    recalc: function() {
      // console.log(this.list.qtty);
      if (this.list.qtty < 0) this.list.qtty = 0;
      // this.$emit("input", this.list.qtty);
      // this.recalcTotal();
      this.recalcTotal();
    },

    incr_one(pos) {
      if (this.list.qtty < 999) {
        for (var item of this.payload.catalogue) {
          if (item.pos == pos) {
            this.list.qtty += 1;
            break;

            // if (item.category != "") {
            //   item.qtty += 1;
            // }
          }
        }
        this.recalc();
      } else {
        alert(i18n.t("alertText.qttyLength"));
      }
    },
    dcr_one(pos) {
      for (var item of this.payload.catalogue) {
        if (item.pos == pos) {
          this.list.qtty -= 1;
          break;
          // if (item.category != "") {
          //   item.qtty -= 1;
          // }
        }
      }
      this.recalc();
    },
    setval: function(event) {
      this.list.qtty = event;
      // console.log(event);
      this.recalc();
    },
    recalcTotal: function() {
      var total = 0,
        tempTotal = 0,
        qty = 0,
        form = this.payload;
      // console.log("total form", form);

      for (var i = 0; i < form.catalogue.length; i++) {
        var curitem = form.catalogue[i];
        if (curitem.type !== "item") continue;
        if (curitem.isOrdered) {
          tempTotal += 1 * curitem.price;
          total = tempTotal.toFixed(2);
          qty += curitem.qtty;
        } else if (curitem.qtty > 0) {
          tempTotal += curitem.qtty * curitem.price;
          total = tempTotal.toFixed(2);
          qty += curitem.qtty;
        }
      }

      form.amount = total;
      document.getElementById("item").innerText = qty;
      document.getElementById("total").innerText = total;
      document.getElementById("Mob_item").innerText = qty;
      document.getElementById("Mob_total").innerText = total;
      this.payload.amount = total;
      // this.$store.dispatch('SET_AMOUNT',total);
      // console.log('current_amount',this.$store.state.current_amount,this.$store.state.current_count);
      // console.log(this.$store.state.currentForm.amount)
    },
  },
};
</script>
